import React, { useState } from "react";
import Link from 'next/link';
import GlobalStyling from '../GlobalStyling';
import Header from "../components/header/Header";


const Home = () => {
    const [nome, setNome] = useState("Danylo");

    return (
        <GlobalStyling>
            <Header name={nome} />
            <Link href="/about">
                <a>To About</a>
            </Link>
        </GlobalStyling>
    )
};

export default Home;
